*, *::before, *::after { box-sizing: border-box; }

html { font-family: sans-serif; line-height: 1.15; -webkit-text-size-adjust: 100%; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section { display: block; }

body { margin: 0; font-weight: 400; line-height: 1.5; color: #212529; text-align: left; background-color: #fff; }

h1, h2, h3, h4, h5, h6 { margin-top: 0; margin-bottom: 0.5rem; }

p { margin-top: 0; margin-bottom: 1rem; }

ol, ul, dl { margin-top: 0; margin-bottom: 1rem; }

ol ol, ul ul, ol ul, ul ol { margin-bottom: 0; }

dt { font-weight: 700; }

dd { margin-bottom: .5rem; margin-left: 0; }

blockquote { margin: 0 0 1rem; }

b, strong { font-weight: bolder; }

small { font-size: 80%; }

sub, sup { position: relative; font-size: 75%; line-height: 0; vertical-align: baseline; }

sub { bottom: -.25em; }

sup { top: -.5em; }

a { color: #007bff; text-decoration: none; background-color: transparent; }

a:hover { color: #0056b3; text-decoration: underline; }

a:not([href]):not([class]) { color: inherit; text-decoration: none; }

a:not([href]):not([class]):hover { color: inherit; text-decoration: none; }

figure { margin: 0 0 1rem; }

img { vertical-align: middle; border-style: none; }

svg { overflow: hidden; vertical-align: middle; }

label { display: inline-block; margin-bottom: 0.5rem; }

button { border-radius: 0; }

button:focus:not(:focus-visible) { outline: 0; }

input, button, select, optgroup, textarea { margin: 0; font-family: inherit; font-size: inherit; line-height: inherit; }

button, input { overflow: visible; }

button, select { text-transform: none; }

[role="button"] { cursor: pointer; }

select { word-wrap: normal; }

button, [type="button"], [type="reset"], [type="submit"] { -webkit-appearance: button; }

button:not(:disabled), [type="button"]:not(:disabled), [type="reset"]:not(:disabled), [type="submit"]:not(:disabled) { cursor: pointer; }

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner { padding: 0; border-style: none; }

input[type="radio"], input[type="checkbox"] { box-sizing: border-box; padding: 0; }

textarea { overflow: auto; resize: vertical; }

fieldset { min-width: 0; padding: 0; margin: 0; border: 0; }

:root { --border_img_h: url(../images/panel/h_border.png); --border_img_v: url(../images/panel/v_border.png); --size_border: 17px; --size_corner: 71px; --corner_img: url(../images/panel/corner.png); --width_decor:82px; --height_decor:43px; }

body { font-family: "Vollkorn", sans-serif; font: 16px/1.5; line-height: 1.5; background: #180f0a; color: #b8afaa; }

.panel { background-color: #180f0a; background-position: 100% 100% !important; background-repeat: no-repeat !important; position: relative; border-radius: 6px; overflow: hidden; --size_border_local: calc(var(--size_border) / 2); --size_corner_local: calc(var(--size_corner) / 2); --width_decor_local: calc(var(--width_decor) / 2); --height_decor_local:calc(var(--height_decor) / 2); }

.panel.md { --size_border_local: calc(var(--size_border) / 2 * 0.75); --size_corner_local: calc(var(--size_corner) / 2 * 0.75); --width_decor_local: calc(var(--width_decor) / 2 * 0.75); --height_decor_local:calc(var(--height_decor) / 2 * 0.75); }

.panel.sm { --size_border_local: calc(var(--size_border) / 2 * 0.5); --size_corner_local: calc(var(--size_corner) / 2 * 0.5); --width_decor_local: calc(var(--width_decor) / 2 * 0.5); --height_decor_local:calc(var(--height_decor) / 2 * 0.5); }

.panel .panel_holder { background-image: var(--border_img_v), var(--border_img_v), var(--border_img_h), var(--border_img_h); background-repeat: repeat-y, repeat-y, repeat-x, repeat-x; background-position: 0 0, 100% 0, 0 0, 0 100%; background-size: var(--size_border_local) auto, var(--size_border_local) auto, auto var(--size_border_local), auto var(--size_border_local); padding: calc(var(--size_border_local) * 2.7); }

.panel .corner { pointer-events: none; position: absolute; top: 0; left: 0; background: var(--corner_img) no-repeat 0 0; background-size: cover; width: var(--size_corner_local); height: var(--size_corner_local); }

.panel .corner.right_top { left: auto; right: 0; transform: scale(-1, 1); }

.panel .corner.right_bottom { top: auto; bottom: 0; left: auto; right: 0; transform: scale(-1, -1); }

.panel .corner.left_bottom { top: auto; bottom: 0; transform: scale(1, -1); }

.panel .decor:before, .panel .decor:after { content: ""; width: var(--width_decor_local); height: var(--height_decor_local); display: block; position: absolute; top: 0; left: 50%; transform: translateX(-50%); background: url(../images/panel/centered_corner.png) no-repeat 0 0; background-size: cover; }

.panel .decor:after { top: auto; bottom: 0; transform: translateX(-50%) scale(-1, -1); }

.panel.horizontal .decor:before, .panel.horizontal .decor:after { background: url(../images/panel/centered_corner_vertical.png) no-repeat 0 0; background-size: cover; width: var(--height_decor_local); height: var(--width_decor_local); top: 50%; left: 2px; position: absolute; transform: translateY(-50%); }

.panel.horizontal .decor:after { top: 50%; left: auto; right: 2px; transform: translateY(-50%) scale(-1, -1); }

.panel_holder { display: flex; flex-direction: column; height: 100%; overflow: hidden; }

.panel_header { flex: 0 0 auto; background: rgba(255, 0, 0, 0.2); padding: calc(var(--size_border_local) * 2.7); position: relative; margin: calc(var(--size_border_local) * -2.7) calc(var(--size_border_local) * -2.7) 0; background: var(--border_img_h) repeat-x 0 100%; background-size: auto var(--size_border_local); margin-bottom: calc(var(--size_border_local) * 2); }

.panel_content { flex: 1 1 auto; overflow: auto; height: 100%; background: rgba(0, 128, 0, 0.2); }

.circle { --height:111px; --width:110px; --width_circle:calc(var(--width) / 2); --height_circle:calc(var(--height) / 2); display: inline-block; width: var(--width_circle); height: var(--height_circle); background: url(../images/circles/circle.png) no-repeat 0 0; background-size: cover; position: relative; }

.circle img { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); max-width: 80%; max-height: 80%; }

.circle.md { --width_circle:calc(var(--width) / 2* 0.75); --height_circle:calc(var(--height) / 2 * 0.75); }

.circle.sm { --width_circle:calc(var(--width) / 2* 0.5); --height_circle:calc(var(--height) / 2 * 0.5); }

a.circle { transition: all 0.15s ease; }

a.circle:hover { transition: all 0.3s ease; filter: saturate(2); }

.rectangle { --size_corner_local:calc(var(--size_corner) / 2.7); position: relative; width: 67px; height: 95px; }

.rectangle .rectangle_mask { width: 100%; height: 100%; position: relative; }

.rectangle .rectangle_mask:before, .rectangle .rectangle_mask:after { content: ""; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: #a5aca7; mix-blend-mode: saturation; z-index: 1; }

.rectangle .rectangle_mask:after { mix-blend-mode: normal; background: none; box-shadow: inset 0 0 7px rgba(0, 0, 0, 0.7); }

.rectangle:before, .rectangle:after { content: ""; position: absolute; top: -1px; left: -1px; width: var(--size_corner_local); height: var(--size_corner_local); background: var(--corner_img) no-repeat; background-size: cover; pointer-events: none; z-index: 2; }

.rectangle:after { left: auto; top: auto; bottom: -2px; right: -1px; transform: scale(-1, -1); }

.rectangle img { z-index: 3; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); max-width: 100%; max-height: 100%; }

a.rectangle img { transition: all 0.15s ease; }

a.rectangle:hover img { transition: all 0.3s ease; filter: saturate(2); }

.justify-content-center { justify-content: center; }

.d-block { display: block; }

.d-flex { display: flex; }

.d-inline-flex { display: inline-flex !important; }

.flex-column { flex-direction: column; }

.flex-wrap { flex-wrap: wrap; }

.align-items-center { align-items: center; }

.d-inline-block { display: inline-block; }

[class*="col-"], .col { position: relative; width: 100%; padding-right: 15px; padding-left: 15px; }

.row { display: flex; flex-wrap: wrap; margin-left: -15px; margin-right: -15px; }

