.btn:not(:disabled):not(.disabled) { cursor: pointer; }

.btn { --px:20px; --py:17px; --size_corner_local:calc(var(--size_corner) / 2.7); --paddings_local:var(--py)  var(--px); --font_size_local:16px; text-align: center; vertical-align: middle; user-select: none; transition: all 0.15s ease; display: inline-block; background: #180f0a; border-radius: 6px; overflow: hidden; border: 0; color: #b8afaa; position: relative; font-size: var(--font_size_local); }

.btn.md { --size_corner_local:calc(var(--size_corner) / 2.7 * 0.75); --font_size_local:calc(16px * 0.75); --paddings_local: calc(var(--py) * 0.75) calc(var(--px) * 0.75); }

.btn.sm { --size_corner_local:calc(var(--size_corner) / 2.7 * 0.5); --font_size_local:calc(16px * 0.65); --paddings_local: calc(var(--py) * 0.65) calc(var(--px) * 0.65); }

.btn > span { display: flex; align-items: center; padding: var(--paddings_local); }

.btn > span:before, .btn > span:after { content: ""; position: absolute; bottom: 0; left: 0; width: var(--size_corner_local); height: var(--size_corner_local); background: var(--corner_img) no-repeat; background-size: cover; transform: scale(1, -1); pointer-events: none; }

.btn > span:after { left: auto; right: 0; transform: scale(-1, -1); }

.btn svg { max-width: 16px; flex: 0 0 16px; margin-right: 5px; }

.btn:before, .btn:after { content: ""; position: absolute; top: 0; left: 0; width: var(--size_corner_local); height: var(--size_corner_local); background: var(--corner_img) no-repeat; background-size: cover; pointer-events: none; }

.btn:after { left: auto; right: 0; transform: scale(-1, 1); }

.btn:hover { color: #b8afaa; color: #fff; text-decoration: none; transition: all 0.3s ease; filter: saturate(2); }
